@custom-media --wide-vp (width >= 1280px);
@custom-media --not-wide-vp (width <= 1279.9px);
@custom-media --middle-vp (768px <= width < 1279.9px);
@custom-media --small-vp (width <= 767.9px);
@custom-media --not-small-vp (width >= 768px);
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

:root {
  --white: #ffffff;
  --black: #000000;
  --black-light: #111111;
  --green: #68b738;
  --grey-dark-special: #222222;
  --grey-dark: #444444;
  --grey-special: #666666;
  --grey-basic_background: #f2f2f2;
  --grey_background: #eaeaea;
  --green_background: rgba(104, 183, 56, 0.85);
  --color-cat_background: #444348;
  --transparent: rgba(0, 0, 0, 0);
  --color-grey_border: #e6e6e6;
  --color-grey-base_border: #d9d9d9;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
}

.page {
  height: 100%;
  font-family: var(--main-font);
}

.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--white);
}

.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__wrapper_center {
  margin: 0 20px;
}
@media (--middle-vp) {
  .page__wrapper_center {
    margin: 0 30px;
  }
}
@media (--wide-vp) {
  .page__wrapper_center {
    width: 1220px;
    margin: 0 auto;
  }
}

.page-main {
  flex-grow: 1;
}

@media (--wide-vp) {
  .page-main_selection {
    margin-bottom: 82px;
  }
}

@media (--wide-vp) {
  .page-main_advantages {
    margin-bottom: 51px;
  }
}

.button {
  font-family: var(--accent-font);
  font-weight: 400;
  width: fit-content;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--white);
  background-color: var(--green);
  border: none;
}
.button--size_wide {
  width: 100%;
}

.page-header {
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (--not-small-vp) {
  .page-header {
    display: grid;
    grid-template-columns: 1fr 2fr;
    padding: 15px 0;
  }
}
@media (--wide-vp) {
  .page-header {
    grid-template-columns: 1fr 1fr;
    padding: 20px 0;
  }
}

.page-header__wrapper-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 17px;
}
@media (--small-vp) {
  .page-header__wrapper-logo {
    position: relative;
  }
  .page-header__wrapper-logo::before {
    content: "";
    position: absolute;
    height: 1px;
    width: 100%;
    left: -20px;
    bottom: 0;
    background-color: var(--color-grey_border);
    box-shadow: 40px 0 0 0 var(--color-grey_border);
  }
}

.page-header__logo-link {
  text-decoration: none;
}

.page-header__logo-link--title {
  margin: 0 auto;
  padding-right: 33px;
}
@media (--not-small-vp) {
  .page-header__logo-link--title {
    padding-right: 0;
    margin-left: 10px;
  }
}

.page-footer {
  background-color: var(--grey-basic_background);
}
.page-footer--within_index {
  --contacts-color: var(--white);
}

.page-footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (--not-small-vp) {
  .page-footer__container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.page-footer__iframe {
  display: block;
  border: none;
  width: 100%;
  height: 362px;
}
@media (--not-small-vp) {
  .page-footer__iframe {
    height: 400px;
  }
}

.page-footer__wrapper_grid {
  background-color: var(--contacts-color, var(--transparent));
}
@media (--wide-vp) {
  .page-footer__wrapper_grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}

@media (--wide-vp) {
  .page-footer__contacts {
    margin-top: 98px;
    grid-row: 1/-1;
    grid-column: 1/-1;
    z-index: 2;
  }
}

@media (--wide-vp) {
  .page-footer__location {
    grid-row: 1/-1;
    grid-column: 1/-1;
  }
}

.page-footer__logo-title {
  padding-top: 39px;
  padding-bottom: 32px;
}

.contacts {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 26px 20px;
}
@media (--not-wide-vp) {
  .contacts {
    background-color: var(--contacts-color, var(--transparent));
  }
}
@media (--middle-vp) {
  .contacts {
    padding: 56px 70px;
  }
}
@media (--wide-vp) {
  .contacts {
    width: 565px;
    height: 198px;
    padding: 56px 80px;
    background-color: var(--white);
  }
}

.contacts__cooperation {
  font-style: normal;
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--black-light);
  text-transform: uppercase;
}

.contacts__text {
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  color: var(--grey-dark);
}

.location__noscript-image {
  display: block;
  object-fit: cover;
  object-position: center center;
  width: 100%;
  height: 362px;
}
@media (--middle-vp) {
  .location__noscript-image {
    height: 400px;
  }
}
@media (--wide-vp) {
  .location__noscript-image {
    max-width: 1440px;
    height: 400px;
    margin: 0 auto;
  }
}

@media (--small-vp) {
  .social {
    width: 100%;
    border-top: 1px solid var(--color-grey-base_border);
    border-bottom: 1px solid var(--color-grey-base_border);
  }
}

.social__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.social__link {
  display: block;
  padding: 20px 15px;
  color: var(--grey-special);
}
.social__link::before {
  content: "";
  display: block;
  mask-repeat: no-repeat;
  mask-position: center;
  background: currentColor;
}
.social__link--vkontakte::before {
  mask-image: url("../icons/sprite.svg#footer-icon-vkontakte");
  width: 23px;
  height: 16px;
}
.social__link--instagram::before {
  mask-image: url("../icons/sprite.svg#footer-icon-instagram");
  width: 16px;
  height: 16px;
}
.social__link--facebook::before {
  mask-image: url("../icons/sprite.svg#footer-icon-facebook");
  width: 22px;
  height: 22px;
}

.copyright {
  font-size: 16px;
  line-height: 18px;
  align-self: flex-start;
  width: 100%;
}
@media (--not-small-vp) {
  .copyright {
    align-self: center;
  }
}

.copyright__link {
  position: relative;
  display: block;
  color: var(--grey-dark);
  text-decoration: none;
  padding-top: 28px;
  padding-bottom: 32px;
}
@media (--not-small-vp) {
  .copyright__link {
    padding-right: 50px;
    text-align: right;
  }
}

.copyright__link::before {
  position: absolute;
  content: "";
  display: block;
  mask-repeat: no-repeat;
  mask-position: center;
  background: currentColor;
  mask-image: url("../icons/sprite.svg#footer-logo-html");
  width: 27px;
  height: 34px;
  top: 22px;
  right: 0;
}

.main-nav {
  font-family: var(--accent-font);
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
}
.main-nav--within_index {
  --link-color: var(--white);
}
@media (--wide-vp) {
  .main-nav--within_index {
    position: relative;
  }
  .main-nav--within_index::before {
    content: "";
    position: absolute;
    z-index: -2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--green_background);
  }
}
@media (--small-vp) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--opened .main-nav__list {
    position: absolute;
    z-index: 2;
    width: 100%;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
  }
}
@media (--not-small-vp) {
  .main-nav {
    margin-right: -17.5px;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
}
@media (--not-small-vp) {
  .main-nav__list {
    flex-direction: row;
    justify-content: space-between;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
@media (--wide-vp) {
  .main-nav__list {
    font-size: 20px;
    line-height: 30px;
    justify-content: flex-end;
  }
}

@media (--small-vp) {
  .main-nav__item {
    position: relative;
    background-color: var(--white);
    box-shadow: 20px 0 0 0 var(--white), -20px 0 0 0 var(--white);
  }
  .main-nav__item::before {
    content: "";
    position: absolute;
    height: 1px;
    width: 100%;
    left: -20px;
    bottom: 0;
    background-color: var(--color-grey_border);
    box-shadow: 40px 0 0 0 var(--color-grey_border);
  }
}

.main-nav__link {
  display: block;
  color: var(--black);
  text-decoration: none;
  text-transform: uppercase;
  padding: 22px 25px 24px;
}
@media (--not-small-vp) {
  .main-nav__link {
    padding: 22px 17.5px 24px;
  }
  .main-nav__link[aria-current] {
    text-decoration: underline;
    text-decoration-thickness: 2px;
    text-underline-offset: 10px;
    text-decoration-color: var(--green);
  }
}
@media (--wide-vp) {
  .main-nav__link {
    color: var(--link-color, var(--black));
  }
  .main-nav__link[aria-current] {
    text-decoration-color: var(--link-color, var(--green));
  }
}

.main-nav__toggle {
  position: absolute;
  top: 10px;
  right: 0;
  width: 34px;
  height: 38px;
  background-color: var(--white);
  cursor: pointer;
  border: none;
}
@media (--small-vp) {
  .main-nav__toggle {
    display: block;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 5px;
  width: 24px;
  height: 1.5px;
  background-color: var(--black);
  box-shadow: 0 7px 0 0 var(--black), 0 14px 0 0 var(--black);
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 18px;
  left: 5px;
  width: 25px;
  height: 1.5px;
  background-color: var(--black);
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.selection {
  display: flex;
  flex-direction: column;
  font-family: var(--accent-font);
  color: var(--white);
}
@media (--small-vp) {
  .selection {
    position: relative;
    text-align: center;
  }
}
.selection::before {
  position: absolute;
  content: "";
  background-image: linear-gradient(to bottom, var(--green_background), var(--green_background)), url("../img/index-background-cat.jpg");
  width: 100%;
  height: 302px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  background-color: var(--color-cat_background);
  z-index: -1;
}
@media (--middle-vp) {
  .selection::before {
    content: initial;
  }
}
@media (--wide-vp) {
  .selection::before {
    right: 0;
    top: 0;
    width: 50%;
    height: 694px;
    background-size: contain;
    background-position: center left;
  }
}

@media (--not-small-vp) {
  .selection__wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}
@media (--wide-vp) {
  .selection__wrapper {
    grid-template-columns: 1fr 1fr;
  }
}

.selection__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  padding-top: 25px;
  margin-top: 0;
  margin-bottom: 25px;
}
@media (--not-small-vp) {
  .selection__title {
    font-size: 60px;
    line-height: 60px;
    color: var(--black);
    grid-column: 1/2;
    grid-row: 1/-1;
    margin-left: 87px;
  }
}
@media (--wide-vp) {
  .selection__title {
    margin-top: 0;
    margin-left: 80px;
  }
}

.selection__slogan {
  font-style: normal;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  text-transform: uppercase;
  margin-top: 0;
}
@media (--not-small-vp) {
  .selection__slogan {
    font-size: 20px;
    line-height: 20px;
    color: var(--black);
    grid-column: 1/2;
    grid-row: 1/-1;
    margin-top: 180px;
    margin-left: 87px;
  }
}
@media (--wide-vp) {
  .selection__slogan {
    margin-top: 230px;
    margin-left: 80px;
  }
}

.selection__image {
  display: block;
  padding-top: 25px;
  margin: 0 auto;
}
@media (--not-small-vp) {
  .selection__image {
    width: 709px;
    height: 609px;
    grid-column: 1/2;
    grid-row: 1/-1;
    z-index: -1;
    margin-top: 220px;
    justify-self: center;
  }
}
@media (--wide-vp) {
  .selection__image {
    width: 552px;
    height: 499px;
    grid-column: 2/-1;
    margin-top: 0;
    margin-left: -150px;
  }
}

.selection__button-link {
  display: block;
  font-size: 14px;
  line-height: 18px;
  padding: 10px 0;
  width: 100%;
  margin-bottom: 20px;
}
@media (--not-small-vp) {
  .selection__button-link {
    box-sizing: border-box;
    font-size: 20px;
    line-height: 20px;
    width: 244px;
    height: 54px;
    padding: 14px 26px;
    grid-column: 1/-1;
    grid-row: 1/-1;
    margin-top: 250px;
    margin-left: 87px;
  }
}
@media (--wide-vp) {
  .selection__button-link {
    margin-top: 300px;
    margin-left: 80px;
  }
}

.advantages {
  color: var(--grey-dark);
  font-size: 14px;
  line-height: 18px;
}
@media (--not-small-vp) {
  .advantages {
    font-size: 16px;
    line-height: 24px;
    color: var(--black);
  }
}

.advantages__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (--wide-vp) {
  .advantages__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.advantages__item {
  position: relative;
  background-color: var(--grey-basic_background);
  padding: 24px 20px 22px;
}
.advantages__item:not(:last-child) {
  margin-bottom: 20px;
}
.advantages__item::before {
  position: absolute;
  content: "";
  width: 50px;
  height: 50px;
  background-color: var(--green);
  border-radius: 50%;
}
@media (--middle-vp) {
  .advantages__item::before {
    width: 200px;
    height: 200px;
    right: 60px;
    top: 92px;
  }
}
@media (--wide-vp) {
  .advantages__item::before {
    width: 100px;
    height: 100px;
    left: 52px;
    top: 47px;
  }
}
@media (--middle-vp) {
  .advantages__item {
    padding: 77px 60px 57px 52px;
  }
}
@media (--wide-vp) {
  .advantages__item {
    padding: 47px 65px 59px 52px;
  }
  .advantages__item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 20px;
  }
}

.advantages__item--slim::after {
  position: absolute;
  content: "";
  background-image: url("../icons/sprite.svg#index-cat-slim");
  background-position: center;
  background-repeat: no-repeat;
}
@media (--small-vp) {
  .advantages__item--slim::after {
    width: 36px;
    height: 50px;
    background-size: 50px 50px;
    left: 29px;
    top: 21px;
  }
}
@media (--middle-vp) {
  .advantages__item--slim::after {
    width: 140px;
    height: 194px;
    background-size: 194px 194px;
    right: 92px;
    top: 81px;
  }
}
@media (--wide-vp) {
  .advantages__item--slim::after {
    width: 70px;
    height: 97px;
    background-size: 97px 97px;
    left: 67px;
    top: 41px;
  }
}

.advantages__item--pro::after {
  position: absolute;
  content: "";
  background-image: url("../icons/sprite.svg#index-cat-pro");
  background-position: center;
  background-repeat: no-repeat;
}
@media (--small-vp) {
  .advantages__item--pro::after {
    width: 67px;
    height: 28px;
    background-size: 67px 67px;
    left: 13px;
    top: 35px;
  }
}
@media (--middle-vp) {
  .advantages__item--pro::after {
    width: 268px;
    height: 112px;
    background-size: 268px 268px;
    right: 28px;
    top: 135px;
  }
}
@media (--wide-vp) {
  .advantages__item--pro::after {
    width: 134px;
    height: 56px;
    background-size: 134px 134px;
    left: 33px;
    top: 67px;
  }
}

.advantages__title {
  display: block;
  font-family: var(--accent-font);
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  color: var(--black);
  text-transform: uppercase;
  margin-top: 10px;
  margin-left: 70px;
}
@media (--not-small-vp) {
  .advantages__title {
    font-size: 36px;
    line-height: 36px;
  }
}
@media (--middle-vp) {
  .advantages__title {
    margin-left: 0;
  }
}
@media (--wide-vp) {
  .advantages__title {
    margin-left: 162px;
    margin-top: 30px;
    margin-bottom: 73px;
  }
}

.advantages__description {
  margin-bottom: 23px;
}
@media (--middle-vp) {
  .advantages__description {
    width: 57%;
  }
}

.advantages__button-link {
  display: block;
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 16px;
  color: var(--black);
  text-decoration: none;
  text-transform: uppercase;
  padding-top: 20px;
}
@media (--small-vp) {
  .advantages__button-link {
    width: 100%;
    border-top: 1px solid var(--color-grey-base_border);
  }
}
@media (--not-small-vp) {
  .advantages__button-link {
    font-size: 20px;
    line-height: 30px;
  }
}

.technologies {
  color: var(--grey-dark);
  font-size: 14px;
  line-height: 18px;
  padding: 20px 0;
}
@media (--not-small-vp) {
  .technologies {
    font-size: 16px;
    line-height: 24px;
  }
}

.technologies__title {
  font-family: var(--accent-font);
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: var(--black);
  margin-top: 0;
  margin-bottom: 40px;
}
@media (--not-small-vp) {
  .technologies__title {
    font-size: 60px;
    line-height: 60px;
  }
}

.technologies__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
  counter-reset: counter-container;
}
@media (--not-small-vp) {
  .technologies__list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.technologies__item {
  position: relative;
  margin-top: 0;
  min-height: 60px;
}
.technologies__item:not(:last-child) {
  margin-bottom: 20px;
}
.technologies__item::before {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  background-color: var(--green);
}
@media (--not-small-vp) {
  .technologies__item::before {
    width: 80px;
    height: 80px;
    left: 0;
    top: 40px;
  }
}
@media (--not-small-vp) {
  .technologies__item {
    width: 50%;
    min-height: 280px;
  }
  .technologies__item::after {
    counter-increment: counter-container;
    content: "" counter(counter-container) "";
    position: absolute;
    font-family: var(--accent-font);
    font-weight: 400;
    font-size: 280px;
    line-height: 280px;
